@import "mixin";

body {
    background-color: #03a9f4;
    margin: 0;
    padding: 0;
}

.container {
    margin: 0;
    padding: 10px;
    max-width: 600px;
}

.new-note {
    background-color: #fff;
    padding: 10px 12px;
    border-radius: 2px;
    @include box-shadow(2);

    &__input {
        width: 100%;
        border: none;
    
        &:focus {
          outline: none;
        }
    }
}

.note-list {
    margin: 40px 0;
}

.note {
    margin-bottom: 20px;
    background-color: #fff;
    padding: 10px 12px;
    border-radius: 2px;
    @include box-shadow(2);

    &__input {
        flex: 1;
        border-top: 0;
        border-right: 0;
        border-left: 0;
        border-bottom: 1px solid #c7c7c7;
    }

    &__text {
        flex: 1;
    }

    &__button {
        background-color: transparent;
        border: 0;
        cursor: pointer;

        &--hidden {
            visibility: hidden;
        }

        &--red {
            color: #f44336;
        }

        &--green {
            color: #4caf50;
        }
    }
}